.reviews {
  position: relative;
  background: #E5E1BE;
  background-size: cover;
  min-height: 537px;
  background-position: center;
  padding: 80px 0;
  color: white;

  &__content-container {

    display: flex;
    flex-direction: column; }

  &__inner {
    display: flex;
    flex-direction: column;
    align-items: flex-start; }

  &__title {
    font-family: 'DzimaaA';
    font-size: 48px;
    line-height: 1.3;
    color: black;
    margin-left: 32px;
    margin-bottom: 40px;
    max-width: 500px; }

  &__card {
    position: relative;
    background: rgba(255,255,255,0.8);
    border-radius: 20px;
    padding: 40px;
    color: #000;
    max-height: 607px;
    width: 558px;
    align-self: flex-end;
    top: -112px; }


  &__slide {
    transform: translateX(100%);
    transition: all 0.8s ease-in-out;
    opacity: 0;
    display: none;

    flex-direction: column;
    align-items: flex-start;
    &.active {
      opacity: 1;
      display: flex;
      transform: translateX(0); } }

  &__avatar {
    width: 74px;
    height: 74px;

    overflow: hidden;
    margin-bottom: 16px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover; } }

  &__text {
    p {
      margin-bottom: 16px;
      line-height: 1.5;
      color: #333;
      font-family: 'MontserratTr'; }
    .reviews__name {
      color: #199B35;
      font-family: 'MontserratTr';
      margin-top: 10px; } }



  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    margin-top: 20px; }





  &__btn {
    background: #199B35;
    color: white;
    border: none;
    border-radius: 16px;
    font-size: 20px;
    width: 232px;
    height: 38px;
    font-family: 'MontserratTr';
    cursor: pointer;
    transition: 0.3s;
    &:hover {
      background: #199B35; } }

  &__arrows {
    display: flex;
    align-items: center;
    gap: 10px; }

  .arrowww {
    background: rgba(0,0,0,0);
    border: 1px solid black;
    border-radius: 50%;
    width: 35px;
    height: 35px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s;
    &:hover {
      background: #199B35;
      color: #fff;
      border: none;
      img {
        filter: brightness(0) invert(1); } } } }


.form-section {
