.form {
  display: flex;
  flex-direction: column;

  input {
    width: 95%;
    padding: 25px;
    margin-bottom: 40px;
    border-radius: 50px;
    border: none;
    font-size: 22px;
    background: #FFFFFF;

    &:focus {
      outline: none;
      box-shadow: 0 0 0 2px rgba(0,0,0,0.1); } }

  button {
    align-self: flex-end;
    padding: 20px 60px;
    border-radius: 40px;
    border: none;
    font-size: 24px;
    cursor: pointer;
    background: #FFFFFF;
    transition: 0.3s;

    &:hover {
      transform: scale(1.05); }

    &:disabled {
      opacity: 0.6;
      cursor: not-allowed;
      transform: none; } } }

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  justify-content: center;
  align-items: center;
  background: rgba(0,0,0,0.5);

  &__content {
    background: white;
    padding: 40px 60px;
    border-radius: 20px;
    font-size: 24px;
    animation: fadeIn 0.3s ease; } }

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.9); }
  to {
    opacity: 1;
    transform: scale(1); } }


